import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import {Paper} from "@material-ui/core";
import IconButton from '@material-ui/core/IconButton';
import ShareMenu from '../shareMenu';
import FavoriteIcon from '@material-ui/icons/Favorite';
import FavoriteBorderIcon from '@material-ui/icons/FavoriteBorder';

const useStyles = makeStyles(theme => ({
  paper : {
    boxShadow: 'none',
    padding: theme.spacing(2),
  },
  title: {
    fontFamily: 'Roboto',
    fontStyle: 'normal',
    fontWeight: 'normal',
    fontSize: '24px',
    lineHeight: '32px',
    color: 'rgba(0, 0, 0, 0.87)',
    marginTop: '2px',
    marginBottom: '2px',
  },
  secondary: {
    marginTop: '2px',
    marginBottom: '2px',
    fontFamily: 'Roboto',
    fontStyle: 'normal',
    fontWeight: 'normal',
    fontSize: '14px',
    lineHeight: '20px',
    color: 'rgba(0, 0, 0, 0.4)',
  }
}));

export default function SidebarArticle(props) {
  const classes = useStyles();
  const data = props.data ? props.data : {fav: false, title: 'Article Title', author: 'SAVON SANOMAT', date: '8 hours ago'};
  const [state, setState] = React.useState(data);

  function addToFavorite(event){
    event.stopPropagation();
    setState({ ...state, fav: !state.fav });
    fetch("/favtoggle",
    {
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json'
      },
      method: "POST",
      body: JSON.stringify({"url": data.link ,"fav":state.fav})
    }).then(res => console.log(res))
  };

  return (
    <Paper className={classes.paper}>
      <p className={classes.title}>{data.title}</p>
      <p className={classes.secondary}>{data.author}<span> - </span>{data.date}</p>
      <IconButton
        color={state.fav !== true ? 'default' : 'secondary'}
        onClick={addToFavorite}
        aria-label="Add to favorites"
        spacing={2}>
        {state.fav === true ? <FavoriteIcon/> : <FavoriteBorderIcon/>}
      </IconButton>
      <ShareMenu url={data.link} title={data.title} />
    </Paper>
  );

}