import React from 'react';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import List from '@material-ui/core/List';
import Typography from '@material-ui/core/Typography';
import PropTypes from 'prop-types';


import Benefit from "./Landingpage/benefit";
import Logo from "./Landingpage/logos"

import { ReactComponent as KlLogo } from '../svg/kauppalehti.svg';
import { ReactComponent as HsLogo } from '../svg/Helsinginsanomat.svg';
import { ReactComponent as KslmLogo } from '../svg/keskisuomalainen.svg';
import { ReactComponent as TsLogo } from '../svg/Turunsanomat.svg';
import { ReactComponent as SsLogo } from '../svg/savonsanomat.svg';
import { ReactComponent as FinnplusLogo} from "../svg/finnplus.svg";
import { ReactComponent as PaperLogo} from "../svg/copy_24px_rounded.svg";
import { ReactComponent as OptionLogo} from "../svg/domain_24px_rounded.svg";
import { ReactComponent as AdLogo} from "../svg/stop_screen_share_24px_rounded.svg";
import { ReactComponent as DeviceLogo} from "../svg/devices_24px_rounded.svg";
import { ReactComponent as CancelLogo} from "../svg/calendar_today_24px_rounded.svg";


const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
  },
  button: {
    marginLeft: theme.spacing(2),
    marginRight: theme.spacing(2),
  },
  headline: {
    textAlign:'center',
    maxWidth:'760px',
    margin: 'auto',
  },
  title: {
    fontFamily: 'Roboto',
    fontStyle: 'normal',
    fontWeight: 900,
    fontSize: '48px',
    lineHeight: '133%',
    letterSpacing: '1.25px',
    color: '#000000',
    margin: 'auto',
    textAlign: 'center',
  },
  info: {
    fontFamily: 'Roboto',
    fontStyle: 'normal',
    fontWeight: 500,
    fontSize: '24px',
    lineHeight: '36px',
    textAlign: 'center',
    letterSpacing: '1.25px',
    color: theme.palette.text.secondary,
  },
  subHeader: {
    fontFamily: 'Roboto',
    fontStyle: 'normal',
    fontWeight: 500,
    fontSize: '24px',
    lineHeight: '36px',
    textAlign: 'center',
    letterSpacing: '1.25px',
    maxWidth: '410px',
    margin: 'auto',

  },
  image:{
    padding: 'none',
    display: 'block',
    maxWidth: '100%',
    maxHeight: '100%',
    position: 'absolute',
    right: '0px',
  },
  payplan: {
    fontFamily: 'Roboto',
    fontStyle: 'normal',
    fontWeight: '500',
    fontSize: '18px',
    lineHeight: '26px',
    letterSpacing: '1.25px',
    '& span' : {
      fontSize: '14px',
      color: theme.palette.text.secondary
    }
  },
  text: {
    fontFamily: 'Roboto',
    fontStyle: 'normal',
    fontWeight: '500',
    fontSize: '14px',
    lineHeight: '26px',
    letterSpacing: '1.25px',
    color: theme.palette.text.secondary
  },
  logo : {
    padding: '6px 8px',
    textAlign: 'center',
  },
  flexContainer: {
    margin: 'auto',
    display: 'flex',
    justifyContent: 'space-evenly'
  },
  CenterPlanBox: {
    height: '350px',
    borderTop: '1px solid rgba(0, 0, 0, 0.2)',
    borderBottom: '1px solid rgba(0, 0, 0, 0.2)',
    width: '33.3%',
    paddingLeft: '3%',
    paddingRight: '3%',
    position: 'relative',
    background: '#F7F7F7',
  },
  PlanBox: {
    height: '350px',
    border: '1px solid rgba(0, 0, 0, 0.2)',
    width: '33.3%',
    paddingLeft: '3%',
    paddingRight: '3%',
    position: 'relative',
  },
  SubscriptionPlanButton: {

  },
  logoImage: {
    maxWidth: '76px',
    maxHeight: '76px',
  },
  headerLeft: {
    marginTop: '10px',
  },
  headerRight: {
    marginTop: '10px',
    textAlign: 'right',
  },
  relative: {
    position: 'relative',
  },
  subscribeHeader: {
    background: '#021E72',
    borderRadius: '4px',
    borderStyle: 'solid',
    fontFamily: 'Roboto',
    fontStyle: 'normal',
    fontWeight: '500',
    fontSize: '14px',
    lineHeight: '16px',
    color: 'white',
    textAlign: 'center',
    letterSpacing: '1.25px',
    textTransform: 'uppercase',
    width: '175px',
  },
  subscribe: {
    position: 'absolute',
    bottom: '10%',
    left: '12%',
    background: '#021E72',
    borderRadius: '4px',
    borderStyle: 'solid',
    fontFamily: 'Roboto',
    fontStyle: 'normal',
    fontWeight: '500',
    fontSize: '14px',
    lineHeight: '16px',
    color: 'white',
    textAlign: 'center',
    letterSpacing: '1.25px',
    textTransform: 'uppercase',
    width: '76%',
    height: '14%',
  }
}));


export default function FrontPage() {
  const classes = useStyles();
  const theme = useTheme();


  return (
    <div className={classes.root}>
      <Grid container spacing={4}>
        <Grid item xs={2} className={classes.headerLeft}>
              <FinnplusLogo className={classes.logo}/>

        </Grid>
        <Grid item xs={10} className={classes.headerRight}>

          <Button href='#pricing' className={classes.button}>
             Pricing
          </Button>
          <Button href='/publisherRegister' className={classes.button}>
             Register as publisher
          </Button>
          <Button href='/signin' className={classes.button}>
             Log in
          </Button>
          <Button href='/signup'  className={classes.subscribeHeader}>
             Subscribe
          </Button>

        </Grid>
        <Grid item xs={12}>
          <div className={classes.headline}>
            <h1 className={classes.title}>The national newspaper subscription service in Finland</h1>
            <h2 className={classes.info}>An online platform where you can access all online Finnish newspapers in one place.</h2>
          </div>
        </Grid>
        <Grid item xs={12}>
          <p className={classes.subHeader}>FiNN+ BENEFITS</p>
        </Grid>

        <Grid item xs={6} className={classes.relative}>
            <img className={classes.image} alt="woman reading newspaper"  src={require('../images/asd.png')}/>
        </Grid>
        <Grid item xs={6}>

            <List className={classes.list}>
              <Benefit benefit="Access all online Finnish newspapers in one place" icon={<PaperLogo/>}/>
              <Benefit benefit="Select from three payment options that suit you best" icon={<OptionLogo/>}/>
              <Benefit benefit="No advertisements" icon={<AdLogo/>}/>
              <Benefit benefit="Access content on any devices" icon={<DeviceLogo/>}/>
              <Benefit benefit="Cancel anytime on a monthly basis" icon={<CancelLogo/>}/>
            </List>

        </Grid>
        <Grid item xs={12} sm={12}>
            <h3 className={classes.subHeader} id="pricing">
              CHOOSE A PLAN<br/>THAT IS RIGHT FOR YOU
            </h3>
        </Grid>
        {/* TODO: Extract this to it's own component */}
        <Grid item xs={8} className={classes.flexContainer}>

          <div id='left' className={classes.PlanBox}>
            <p className={classes.payplan}>SINGLE PAYMENT</p>
            <p className={classes.payplan}>50 cents <span>(per article)</span></p>
            <p className={classes.text}>- Access articles from all online Finnish newspapers</p>
            <p className={classes.text}>- Read on any device</p>
            <Button href='/signup?method=2' className={classes.subscribe}>
                 Subscribe
            </Button>
          </div>
          <div id='center' className={classes.CenterPlanBox}>
            <p className={classes.payplan}>PACKAGE SUBSCRIPTION</p>
            <p className={classes.payplan}>5€ <span>(per month)</span></p>
            <p className={classes.text}>- Access 15 articles from all online Finnish newspapers</p>
            <p className={classes.text}>- Read on any device</p>
            <p className={classes.text}>- Cancel at anytime</p>
            <Button href='/signup?method=1' className={classes.subscribe}>
                 Subscribe
            </Button>
          </div>
          <div id='right' className={classes.PlanBox}>
            <p className={classes.payplan}>MONTHLY SUBSCRIPTION</p>
            <p className={classes.payplan}>5€ <span>(per month)</span></p>
            <p className={classes.text}>- Unlimited access to all online Finnish newspapers</p>
            <p className={classes.text}>- Read on any device</p>
            <p className={classes.text}>- Cancel at anytime</p>
            <Button href='/signup?method=0' className={classes.subscribe}>
                 Subscribe
            </Button>
          </div>
        </Grid>
        <Grid item xs={12}>
          <h3 className={classes.subHeader}>NEWSPAPERS</h3>
        </Grid>
        <Grid item xs={12}>
          <div className={classes.flexContainer}>
            <Logo logo={<KlLogo className={classes.logoImage}/>} name="Kauppalehti"/>
            <Logo logo={<HsLogo className={classes.logoImage}/>} name="Helsingin sanomat" />
            <Logo logo={<TsLogo className={classes.logoImage}/>} name="Turun sanomat" />
            <Logo logo={<SsLogo className={classes.logoImage}/>} name="Savon Sanomat" />
            <Logo logo={<KslmLogo className={classes.logoImage}/>} name="Keskisuomalainen" />
          </div>
        </Grid>
      </Grid>
    </div>
  );
}
