import React from 'react';
import Drawer from "@material-ui/core/Drawer";
import { makeStyles } from '@material-ui/core/styles';
import Divider from "@material-ui/core/Divider";
import List from "@material-ui/core/List";
import Icon from '@material-ui/core/Icon';
import DrawerItem from "./DrawerItem";



const drawerWidth = 240;

const useStyles = makeStyles(theme => ({

  drawer: {
    width: drawerWidth,
    marginLeft: -drawerWidth,
  },
  drawerPaper: {
    width: drawerWidth,
  },
  drawerHeader: {
    padding: theme.spacing(0, 1),
    ...theme.mixins.toolbar,

  }
}));

export default function DrawerMenu(props) {
  const classes = useStyles();
  const open = props.open === undefined ?  true : props.open;

  return (
    <Drawer
      className={classes.drawer}
      variant="persistent"
      anchor="left"
      open={open}
      classes={{
        paper: classes.drawerPaper,
      }}
    >
      <div className={classes.drawerHeader}>
      </div>
      <Divider />
      <List>
        {props.categories.map(obj => (
          <DrawerItem name={obj.name} icon={obj.icon} onClick={props.onClick} key={obj.name}/>
        ))}
      </List>
      <Divider />
      <List>
        {props.providers.map(obj => (
          <DrawerItem name={obj.name} icon={obj.icon} onClick={() => {window.location.href = obj.link}} key={obj.name}/>
        ))}
      </List>
      <Divider />
      <List>
        <DrawerItem name="Register as a publisher" icon={<Icon/>} onClick={() => {window.location.href = "/publisherRegister"}}/>
        <DrawerItem name="About" icon={<Icon/>} onClick={() => {}}/>
      </List>
    </Drawer>
  );
}