import React from 'react';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import FavoriteIcon from '@material-ui/icons/Favorite';
import FavoriteBorderIcon from '@material-ui/icons/FavoriteBorder';
import IconButton from '@material-ui/core/IconButton';
import DoneIcon from '@material-ui/icons/Done';
import ShareMenu from './shareMenu';
import ReactTooltip from 'react-tooltip'
import Link from "@material-ui/core/Link";



const useStyles = makeStyles(theme => ({
  root: {
    padding: theme.spacing(2),
    paddingTop: '0px',
    margin: 'auto',
  },
  title: {
    fontFamily: 'Roboto',
    fontStyle: 'normal',
    fontWeight: 'normal',
    fontSize: '24px',
    lineHeight: '32px',
    color: 'rgba(0, 0, 0, 0.87)',
    marginTop: '2px',
    marginBottom: '2px',
  },
  secondary: {
    marginTop: '2px',
    marginBottom: '2px',
    fontFamily: 'Roboto',
    fontStyle: 'normal',
    fontWeight: 'normal',
    fontSize: '14px',
    lineHeight: '20px',
    color: 'rgba(0, 0, 0, 0.4)',
  },
  heading: {
    fontSize: theme.typography.pxToRem(24),
    fontWeight: theme.typography.fontWeightRegular,
  },
  panel: {
    border: '0.5px solid #C4C4C4',
    boxSizing: 'border-box',
    borderRadius: '3px',
    boxShadow: 'none',
    width: '100%',
  },
  image: {
    maxHeight: '128px',
    maxWidth: 160,
    top:0,
  },
  img: {
    display: 'block',
    maxWidth: '100%',
    height: '100%',
    maxHeight: '128px',
    top:0
  },
  hover:{
    position:'relative',
    visibility: 'visible',
    '&:hover':{
      visibility:'visible',
      backgroungColor:'red'
    },
    title: {
      fontFamily: 'Roboto',
      fontStyle: 'normal',
      fontWeight: 'normal',
      fontSize: '24px',
      lineHeight: '32px',
      color: 'rgba(0, 0, 0, 0.87)',
    }

  }
}));

export default function Article(props) {
  const classes = useStyles();
  const theme = useTheme();
  const data = props.data ? props.data : {fav: false, title: 'Article Title', author: 'SAVON SANOMAT', date: '8 hours ago', preview: 'Article preview'};
  const [state, setState] = React.useState(data);




  function addToFavorite(event){
    event.stopPropagation();
    setState({ ...state, fav: !state.fav });
    fetch("/favtoggle",
    {
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json'
      },
      method: "POST",
      body: JSON.stringify({"url": data.link ,"fav":state.fav})
    }).then(res => console.log(res))
  };
  
  

  return (
    <div className={classes.root}>
        <ExpansionPanel className={classes.panel}>
        <ExpansionPanelSummary
          expandIcon={<ExpandMoreIcon style={{marginTop:100}} />}
          aria-controls="panel1a-content"
          id="panel1a-header">
            <Grid container spacing={2}>
              <Grid item>
                <div className={classes.image}>

                    <img className={classes.img} alt="Article image" src={data.img} />

                </div>
              </Grid>
              <Grid item xs={12} sm container>
                <Grid item xs container direction="column" spacing={2}>
                  <Grid item xs>
                    <Link href={data.link} className={classes.title} color='inherit'>
                      <Typography gutterBottom variant="subtitle1" className={classes.title}>
                        {data.title}
                      </Typography>
                    </Link>
                    <Typography  variant="body2" gutterBottom color="textSecondary" className={classes.secondary}>
                      {data.author}
                      <span> - </span>
                      <Typography  component='span' variant="body2" color="textSecondary" >
                         {data.date}
                      </Typography>
                    </Typography>
                    <IconButton
                      color={state.fav === false ? 'default' : 'secondary'}
                      onClick={addToFavorite}
                      aria-label="Add to favorites"
                      spacing={2}>
                      {state.fav === true ? <FavoriteIcon/> : <FavoriteBorderIcon/>}
                    </IconButton>
                    <ShareMenu url={data.link} title={data.title} />
                    {data.read &&
                        <span data-tip="read">
                          <IconButton style={{color:'green'}} aria-label="done" spacing={2}>
                            <DoneIcon />
                          </IconButton>
                          <ReactTooltip />
                        </span>}
                           
                      
                  </Grid>
                </Grid>
              </Grid>
            </Grid>
          
           </ExpansionPanelSummary>
        <ExpansionPanelDetails>
         <Typography>
            {data.preview}
          </Typography>
        </ExpansionPanelDetails>
      </ExpansionPanel>
    </div>
  );
}