import React from 'react';
import clsx from 'clsx';
import { makeStyles } from '@material-ui/core/styles';
import {Paper} from "@material-ui/core";
import Avatar from "@material-ui/core/Avatar";
import Button from "@material-ui/core/Button";
import AccountCircleIcon from '@material-ui/icons/AccountCircle';



const useStyles = makeStyles(theme => ({

  root: {
    maxWidth: '370px',
    maxHeight: '240px',
  },
  text: {
    flexGrow: 1,
    fontFamily: 'Roboto',
    fontStyle: 'normal',
    fontWeight: 'normal',
    fontSize: '13px',
    lineHeight: '14px',
    letterSpacing: '1.25px',
    marginLeft: '15px',
    marginBottom: '0px',
    marginTop: '5px',
  },
  avatar: {
    width: '60px',
    height: '60px',
    background: 'white',
  },
  div: {
    padding: theme.spacing(2),
    position: 'relative',
  },
  mainText: {
    fontWeight: '500 !important',
    fontSize: '14px !important',
    color: '#000000',
    marginTop: '13px',

  },
  fugly: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'flex-start'
  },
  userInfo: {
    display: 'flex',
    maxHeight: '106px',
    flexWrap: 'wrap',
    borderBottom: 'solid 1px rgba(33, 33, 33, 0.08)',
  },
  payInfo: {
    height: '130px',
  },
  buttons: {
    marginTop: '30px',
    display: 'flex',
    justifyContent: 'space-between',
  },
  button: {
    color: '#00069F',
    width: '33%',
  },
  button2: {
    color: '#00069F',
    width: '40%',
    borderColor: '#00069F',
  },
  icon: {
    width: '60px',
    height: '60px',
  }
}));

export default function ProfileCard(props) {
  const classes = useStyles();
  const data = props.data ? props.data : {name: 'User Name', email: 'username@gmail.com', plan: 'Single payment, 4 articles left', image:'https://material-ui.com/static/images/avatar/1.jpg'};

  let ProfilePic
  if (data.image) {
    ProfilePic = <Avatar className={classes.avatar} src={data.image} alt={data.name}/>
  } else {
    ProfilePic = (
      <Avatar className={classes.avatar} alt={data.name}>
        <AccountCircleIcon className={classes.icon} htmlColor='#5F5F5F'/>
      </Avatar>
    )
  }

  return (
    <Paper className={classes.root}>
      <div className={clsx(classes.div, classes.userInfo)}>
        {ProfilePic}
        <div className={classes.fugly}>
          <p className={clsx(classes.text, classes.mainText)}>{data.name}</p>
          <p className={classes.text}>{data.email}</p>
        </div>
      </div>
      <div className={clsx(classes.div, classes.payInfo)}>
        <p className={clsx(classes.text, classes.mainText)}>My plan</p>
        {
          data.subscription_end ? <p className={classes.text}>Monthly subscription ends {data.subscription_end}</p> :
          data.package_end ? <p className={classes.text}>Package subscription, {data.package_end} articles left </p> :
          data.tokens ? <p className={classes.text}>Single payment, {data.tokens} articles left </p> :
          <p className={classes.text}>No active plan</p>
        }
        <p className={classes.text}>{data.plan}</p>
        <div className={classes.buttons}>
          <Button href='/profile' className={classes.button}>My Profile</Button>
          <Button href='/logout' variant='outlined' className={classes.button2}>Log out</Button>
        </div>
      </div>
    </Paper>
  );
}