import React from 'react';
import clsx from "clsx";
import { makeStyles } from '@material-ui/core/styles';
import CssBaseline from '@material-ui/core/CssBaseline';
import Article from "./article";
import DrawerMenu from "./Dashboard/Drawer";
import Bar from "./Dashboard/AppBar";

import {Paper} from "@material-ui/core";
import SidebarArticle from "./Dashboard/SideBarArticle";
import Grid from "@material-ui/core/Grid";

import SubjectIcon from '@material-ui/icons/Subject';
import EuroSymbolIcon from '@material-ui/icons/EuroSymbol';
import RowingIcon from '@material-ui/icons/Rowing';
import DeveloperBoardIcon from '@material-ui/icons/DeveloperBoard';
import FitnessCenterIcon from '@material-ui/icons/FitnessCenter';
import LocalMoviesIcon from '@material-ui/icons/LocalMovies';
import AccountBalanceIcon from '@material-ui/icons/AccountBalance';

import { ReactComponent as SsLogo } from '../svg/savonsanomat.svg';
import { ReactComponent as KlLogo } from '../svg/kauppalehti.svg';
import { ReactComponent as HsLogo } from '../svg/Helsinginsanomat.svg';
import { ReactComponent as KslmLogo } from '../svg/keskisuomalainen.svg';
import { ReactComponent as TsLogo } from '../svg/Turunsanomat.svg';


const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
    flexWrap: 'nowrap',
  },
  drawerHeader: {
    padding: theme.spacing(0, 1),
    ...theme.mixins.toolbar,

  },
  content: {
    margin: 'auto',
    flexGrow: 1,
    padding: theme.spacing(2),
  },
  paper: {
    boxShadow: 'none',
    border: '0.5px solid #C4C4C4',
  },
  articleContainer: {
    minWidth: '700px',
    maxWidth: '1200px',

    flexShrink: 1,
  },
  recentContainer: {
    minWidth: '300px'
  },
  sideBarDummy: {
    width: '240px',
    flexShrink: 0,
  },
  logo: {
    maxWidth: '36px',
    maxHeight: '36px',
  },
  heading: {
    fontFamily: 'Roboto',
    fontStyle: 'normal',
    fontWeight: 'normal',
    fontSize: '24px',
    lineHeight: '32px',
  },
  newsHeader: {
    padding: theme.spacing(2)
  },
  recentHeader: {
    padding: theme.spacing(2),
    paddingLeft: '0px',
  }
}));


// TODO: Make this use class approach
// TODO: Add state for filter by category
// TODO: Send highlighted category as prop to drawer, send callback to drawer for filtering
export default function DBoard() {
  const classes = useStyles();
  const [open, setOpen] = React.useState(true);
  const [catFilter, setCatFilter] = React.useState('All News');
  const cats = [
    {name: 'All News', icon: <SubjectIcon/>},
    {name: 'Politics', icon: <AccountBalanceIcon/>},
    {name: 'Sports', icon: <RowingIcon/>},
    {name: 'Economy', icon: <EuroSymbolIcon/>},
    {name: 'Technology', icon: <DeveloperBoardIcon/>},
    {name: 'Health', icon: <FitnessCenterIcon/>},
    {name: 'Entertainment', icon: <LocalMoviesIcon/>}
    ];
  const providers = [
    {name: 'Helsingin Sanomat', icon: <HsLogo className={classes.logo}/>, link: '/hs/'},
    {name: 'Kauppalehti', icon: <KlLogo className={classes.logo}/>, link: '/kl/'},
    {name: 'Keskisuomalainen', icon: <KslmLogo className={classes.logo}/>, link: '/ks/'},
    {name: 'Savon Sanomat', icon: <SsLogo className={classes.logo}/>, link: '/ss/'},
    {name: 'Turun Sanomat', icon: <TsLogo className={classes.logo}/>, link: '/ts/'}
    ];
  const articles = window.obj ? window.obj.articles : [];
  const userdata = window.obj ? window.obj.user : {};

  function catFilterRule(el) {
    if (catFilter === 'All News') return true;
    return (el.name === catFilter)
  }

  const handleDrawerToggle = () => {
    if (open) {
      setOpen(false);
    } else {
      setOpen(true)
    }
  };

  function handleSidebarClick(param) {
    setCatFilter(param)
  }

  return (
    <div className={classes.root}>
      <CssBaseline />

      <Bar open={open} user={userdata} onClick={handleDrawerToggle}/>
      <DrawerMenu open={open} categories={cats} providers={providers} onClick={handleSidebarClick}/>

      <main className={classes.content}>
        <div className={classes.drawerHeader}/>
        <Grid container className={classes.root} spacing={2} justify="center">
          <Grid item className={classes.sideBarDummy}/>
          <Grid item className={classes.articleContainer}>
            {articles.filter(catFilterRule).map((obj, i) => (
              <div key={i}>
                <h3 key={i} className={clsx(classes.heading, classes.newsHeader)}>{obj.name}</h3>
                {obj.content.map((artData, i) => (<Article data={artData} key={i}/>))}
              </div>
            ))}
          </Grid>
          <Grid item className={classes.recentContainer}>
            <h3 className={clsx(classes.heading, classes.recentHeader)}>Recent reads</h3>
            <Paper className={classes.paper}>
              {userdata.recentArticles.map((artData, i) => (<SidebarArticle data={artData} key={i}/>))}
            </Paper>
          </Grid>
        </Grid>
      </main>
    </div>
  );
}